"use client";

import React, { useEffect, useState } from "react";

export default function UserAdmin() {
  const [users, setUsers] = useState<any[]>([]);
  const [sortColumn, setSortColumn] = useState<string | null>(null);
  const [sortDirection, setSortDirection] = useState<"asc" | "desc">("asc");

  // Datos de prueba
  useEffect(() => {
    setUsers([
      { id: 1, name: "Alice Johnson", email: "alice@email.com", role: "Admin", status: "Active" },
      { id: 2, name: "Bob Martinez", email: "bob@email.com", role: "User", status: "Inactive" },
      { id: 3, name: "Carlos Díaz", email: "carlos@email.com", role: "User", status: "Active" },
    ]);
  }, []);

  const handleSort = (columnKey: string) => {
    if (columnKey === "actions") return; // ❌ no ordenar actions

    if (sortColumn === columnKey) {
      // toggle direction
      setSortDirection(sortDirection === "asc" ? "desc" : "asc");
    } else {
      setSortColumn(columnKey);
      setSortDirection("asc");
    }
  };

  // Ordenar antes de renderizar
  const sortedUsers = [...users].sort((a, b) => {
    if (!sortColumn) return 0;

    const aVal = String(a[sortColumn]).toLowerCase();
    const bVal = String(b[sortColumn]).toLowerCase();

    if (aVal < bVal) return sortDirection === "asc" ? -1 : 1;
    if (aVal > bVal) return sortDirection === "asc" ? 1 : -1;
    return 0;
  });

  return (
    <div className="w-full flex justify-center mt-16">
      <div className="w-[80%]">

        <h1 className="text-3xl font-bold mb-6">User Administration</h1>

        <div className="overflow-x-auto rounded-xl shadow border border-gray-200 bg-white">

          <table className="w-full text-left border-collapse">
            <thead className="bg-gray-100">
              <tr>
                {["id", "name", "email", "role", "status", "actions"].map((col) => (
                  <th
                    key={col}
                    className={`px-6 py-3 font-semibold uppercase text-sm ${
                      col !== "actions" ? "cursor-pointer select-none" : ""
                    }`}
                    onClick={() => handleSort(col)}
                  >
                    <div className="flex items-center gap-1">
                      {col}
                      {sortColumn === col && (
                        <span>{sortDirection === "asc" ? "▲" : "▼"}</span>
                      )}
                    </div>
                  </th>
                ))}
              </tr>
            </thead>

            <tbody>
              {sortedUsers.map((u) => (
                <tr key={u.id} className="border-t">
                  <td className="px-6 py-4">{u.id}</td>
                  <td className="px-6 py-4">{u.name}</td>
                  <td className="px-6 py-4">{u.email}</td>
                  <td className="px-6 py-4">{u.role}</td>
                  <td className="px-6 py-4">{u.status}</td>

                  <td className="px-6 py-4 flex gap-3">
                    <button className="px-3 py-1 bg-blue-600 text-white rounded-md text-sm">
                      Update
                    </button>
                    <button className="px-3 py-1 bg-red-600 text-white rounded-md text-sm">
                      Delete
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>

          </table>
        </div>
      </div>
    </div>
  );
}
